.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;
    background: url(/images/star.svg) no-repeat center;

    &:not(:last-child) {
      margin-right: 4px;
    }
  }

  @for $index from 1 through 5 {
    &--#{$index} &__star:nth-child(-n + #{$index}) {
      background-image: url(/images/star-active.svg);
    }
  }
}
